.pricing {
	padding-top: 78px;
	background-color: #f7f8f9;
	padding: 78px 0 50px;
	&__header {
		@include theme_title; }
	&__text {
		@include theme_text; }
	&__header + &__text {
		width: 468px;
		margin: 13px auto 50px;
		text-align: center; }
	&__items {
		width: 100%;
		height: 100%;
		@include clr; }
	&__item {
		width: 33%;
		float: left;
		margin: auto;
		text-align: center; }
	&__head {
		position: relative;
		font-size: 14px;
		font-weight: 700;
		text-transform: uppercase;
		&:before {
			content: "";
			display: block;
			margin: 0 auto 15px;
			background-color: #ffffff;
			border-radius: 50%;
			width: 69px;
			height: 69px; }
		&:after {
			position: absolute;
			content: "";
			display: block;
			top: 17%;
			left: 45%; }
		&.standard {
			color: #afbe96;
			&:after {
				@include s(like); } }
		&.popular {
			color: #627787;
			&:after {
				top: 22%;
				@include s(heart); } }
		&.premium {
			color: #f3897f;
			&:after {
				left: 44%;
				@include s(star); } } }
	&__value {
		color: #000000;
		font-size: 2.8125em;
		font-weight: 500;
		&:after {
			content: "per month";
			display: block;
			margin: 0 auto;
			width: 30%;
			color: #a4a4a4;
			font-size: 0.4em;
			font-weight: 400;
			padding-bottom: 30px;
			border-bottom: 1px solid #ddd; } }
	&__list {
		margin-top: 30px; }
	&__components {
		font-size: 13px;
		line-height: 30px;
		color: #a4a4a4;
		font-weight: 400;
		&.is-active {
			color: #404040;
			font-weight: 500; } }
	&__about {
		@include theme_text;
		font-size: 13px;
		width: 528px;
		margin: 92px auto 0;
		text-align: center; }
	&__button {
		display: block;
		margin: 40px auto 100px;
		background-color: #f6a79f;
		border-color: transparent;
		&:hover {
			border: 3px solid #fdb352; } } }
